import React, { useEffect, Suspense, useRef, useState } from "react"
import "@nosferatu500/react-sortable-tree/style.css"
import { useNavigate } from "react-router-dom"
import Router from "./router/Router"
import Spinner from "./@core/components/spinner/Fallback-spinner"
import { store } from "./redux/store"
import { useDispatch, useSelector } from "react-redux"
import { getDomainInfo } from "@store/domainInfo"
import { getMyNotifications } from "@store/notifications"
import { isUserLoggedIn } from "@utils"
import {
  replacePrimaryColor,
  replacePrimaryColorLate
} from "@src/utility/CssColorReplacer"
import { getAppMenu } from "@src/views/apps/menu/store/my_menu"
import { getUnits } from "@src/views/apps/unit/store/my_unit"

import { library } from "@fortawesome/fontawesome-svg-core"
import * as Icons from "@fortawesome/free-solid-svg-icons"
import Swal from "sweetalert2"
const iconList = Object.keys(Icons)
  .filter((key) => key !== "fas" && key !== "prefix")
  .map((icon) => Icons[icon])

library.add(...iconList)

const App = () => {
  const dispatch = useDispatch()
  const effectRan = useRef(false)
  const navigate = useNavigate()

  function getFaviconEl() {
    return document.getElementById("favicon")
  }

  async function pingServers() {
    let serversList = JSON.parse(sessionStorage.getItem("serversList") ?? "[]")
    await Promise.all(
      serversList?.map(async (url) => {
        try {
          const response = await fetch(url, { method: "HEAD" })
          // console.log(lo)
          return response.ok
        } catch (error) {
          console.error(error)
          // return false
        }
      })
    )
  }

  useEffect(() => {
    if (isUserLoggedIn() !== null && window.location.pathname !== "/error") {
      const timeToFetch = 15 //in seconds
      let timer = null

      const handleVisibilityChange = () => {
        const datetime = new Date()
        if (document.hidden) {
          localStorage.setItem("inactive", Date.parse(datetime) / 1000)
          clearInterval(timer)
        } else {
          const lastTimeStr = localStorage.getItem("inactive")
          const timestamp = parseInt(lastTimeStr, 10)
          const lastTime = new Date(timestamp * 1000)
          const diffInSeconds =
            Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
          if (diffInSeconds >= timeToFetch) {
            console.log(
              `Fetching data after ${diffInSeconds} seconds of inactivity`
            )
            store
              .dispatch(getMyNotifications({ unread_count: 1 }))
              .then((res) => {
                if (res?.payload) {
                  const count = res?.payload[0]?.count_unread ?? 0
                  if (count) {
                    const countFix =
                      count > 0 && count <= 20 ? `(${count})` : "(20+)"
                    document.title = `${countFix} ${document.title.replaceAll(
                      countFix,
                      ""
                    )}`
                    pingServers()
                  }
                }
              })
          } else {
            timer = setInterval(() => {
              const diffInSeconds =
                Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
              if (diffInSeconds >= timeToFetch) {
                console.log(
                  `Fetching data after ${diffInSeconds} seconds of inactivity`
                )
                store
                  .dispatch(getMyNotifications({ unread_count: 1 }))
                  .then((res) => {
                    if (res?.payload) {
                      const count = res?.payload[0]?.count_unread ?? 0
                      if (count) {
                        const countFix =
                          count > 0 && count <= 20 ? `(${count})` : "(20+)"
                        document.title = `${countFix} ${document.title.replaceAll(
                          countFix,
                          ""
                        )}`
                      }
                      pingServers()
                    }
                  })
                clearInterval(timer)
              }
            }, 1000)
          }
        }
      }

      document.addEventListener("visibilitychange", handleVisibilityChange)

      return () => {
        clearInterval(timer)
        document.removeEventListener("visibilitychange", handleVisibilityChange)
      }
    }
  }, [])

  useEffect(() => {
    if (!effectRan.current) {
      if (navigator.onLine) {
        dispatch(getDomainInfo({})).then((res) => {
          if (res?.meta?.requestStatus === "rejected") {
            // console.error(res)
            console.error("Not Registered")
            Swal.fire({
              icon: "error",
              title: "Connection Failed!",
              text: "There is a problem connecting to the server or this app is not yet registered with Saba Official.",
              confirmButtonText: "Refresh",
              customClass: {
                confirmButton: "btn btn-primary"
              }
            }).then((result) => {
              if (result.isConfirmed) {
                location.reload()
              }
            })
          } else {
            const logoResp = res.payload?.app?.logo
            if (
              logoResp?.favicon ||
              res.payload?.unit?.unit_app_attributes?.favicon
            ) {
              const favicon = getFaviconEl()
              favicon.href =
                res.payload?.unit?.unit_app_attributes?.favicon ??
                logoResp.favicon
            }

            const primaryColorBe =
              res.payload?.unit?.unit_app_attributes?.layout?.primaryColor ??
              res.payload?.app?.setting?.primaryColor
            if (primaryColorBe) {
              replacePrimaryColor(primaryColorBe)
              setTimeout(() => {
                replacePrimaryColorLate(primaryColorBe)
              }, 500)
            }
            if (window.location.pathname !== "/error") {
              if (isUserLoggedIn() !== null) {
                store.dispatch(getAppMenu({})).then((res) => {
                  if (res?.meta?.requestStatus !== "rejected") {
                    store.dispatch(getUnits({}))
                    setTimeout(() => {
                      store
                        .dispatch(getMyNotifications({ unread_count: 1 }))
                        .then((res) => {
                          if (res?.payload) {
                            const count = res?.payload[0]?.count_unread ?? 0
                            if (count) {
                              const countFix =
                                count > 0 && count <= 20
                                  ? `(${count})`
                                  : "(20+)"
                              document.title = `${countFix} ${document.title.replaceAll(
                                countFix,
                                ""
                              )}`
                            }
                          }
                        })
                    }, 2000)
                  } else {
                    localStorage.setItem("lastUrl", window.location.href)
                    navigate("/error")
                  }
                })
              } else {
                if (
                  (window.location.pathname !== "/login" ||
                    window.location.pathname?.indexOf("/public") !== -1) &&
                  window.location.pathname !== "/reset-password" &&
                  window.location.pathname !== "/forgot-password" &&
                  window.location.pathname !== "/register"
                ) {
                  navigate("/login")
                } else {
                  if (window.location.pathname === "/reset-password") {
                    navigate(window.location.pathname + window.location.search)
                  } else if (window.location.pathname === "/forgot-password") {
                    navigate("/forgot-password")
                  } else if (
                    window.location.pathname === "/register" &&
                    (res.payload?.app?.setting?.enable_registration ||
                      res.payload?.unit?.unit_app_attributes
                        ?.enable_registration)
                  ) {
                    navigate("/register")
                  } else {
                    navigate("/login")
                  }
                }
              }
            }
          }
        })
      } else {
        console.error("Offline")
        Swal.fire({
          icon: "error",
          title: "Youre not connected to internet",
          text: "Please check your connection to the internet.",
          confirmButtonText: "Refresh",
          customClass: {
            confirmButton: "btn btn-primary"
          }
        }).then((result) => {
          if (result.isConfirmed) {
            location.reload()
          }
        })
      }
    }

    return () => {
      effectRan.current = true
    }
  }, [])

  return (
    <Suspense fallback={<Spinner />}>
      <Router />
    </Suspense>
  )
}

export default App
